import React,{useState} from 'react';
import Header from '../components/header/Header'
import Sidebar from '../components/sidebar/Sidebar';
import Content from '../components/content/Content'

const Home = (props) => {
    const [sidebar,setSidebar] = useState(false);
    const [left,setLeft] = useState(false);
    console.log(left);

    const showSidebar = () => {
        setSidebar(!sidebar);
        setLeft(!left);
    }
    return ( 
        <div style={{width:'100%',height:'100%'}}>
            <Header click={showSidebar}></Header>
            <Sidebar sidebar={sidebar} showSidebar={showSidebar}></Sidebar>
            <Content left={left}>
                <div style={{margin:'30px'}} className="row">
                    <button style={{fontSize:'30px',borderRadius:'7px',backgroundColor:' #0058ff',color:'white',padding:'10px 0'}} className="col mr-5">Compra</button>
                    <button style={{fontSize:'30px',borderRadius:'7px',backgroundColor:' #0058ff',color:'white',padding:'10px 0'}} className="col ml-5 mr-5">Venta</button>
                    <button style={{fontSize:'30px',borderRadius:'7px',backgroundColor:' #0058ff',color:'white',padding:'10px 0'}} className="col ml-5">Cobro</button>
                </div>
            </Content>
            
        </div>
    );
}
 
export default Home;