import React,{useState} from 'react';
import Header from '../components/header/Header'
import Sidebar from '../components/sidebar/Sidebar';
import Content from '../components/content/Content'



const Cobros = () => {
    const [sidebar,setSidebar] = useState(false);
    const [left,setLeft] = useState(false);
    console.log(left);

    const showSidebar = () => {
        setSidebar(!sidebar);
        setLeft(!left);
    }
    
    return ( 
        <div style={{width:'100%',height:'100%'}}>
            <Header click={showSidebar}></Header>
            <Sidebar sidebar={sidebar} showSidebar={showSidebar}></Sidebar>
            <Content left={left}>
                <div style={{margin:'30px',backgroundColor:'white',paddingBottom:'300px'}}>
                    <div style={{backgroundColor:'#0058ff',color:'white',fontWeight:'bold',padding:'5px'}}>
                        Cobros
                    </div>
                    <div style={{padding:'20px 0 0 20px'}}>
                        <button className="btn btn-primary">Agregar</button>
                        <input type="text" name="buscar" placeholder="Buscar" className="" style={{marginLeft:'700px'}} />
                    </div>
                    <div ></div>
                </div>
            </Content>
            
        </div>
     );
}
 
export default Cobros;